@import "../partials/variables";

//Theme 12
#atw_style12{
    .atw__title12{
        &::before{
            width: 80px;
            background: $theme3;
        }
    }
    .atw--single_item{
        transition: $transition;
        &.atw_card_rounded{
            border-radius: 5px;
            overflow: hidden;
        }
        .atw_item_bottom{
            .atw_item_header{
                margin-bottom: 20px;
                padding: 0 20px;
                .atw_item_title a:hover{
                    color: $theme3;
                }
                .product-weight{
                    font-size: 14px;
                    color: #9192a3;
                    margin-top: 3px;
                }
            }
            .atw_item_info{
                display: flex;
                justify-content: space-between;
                padding: 0 20px;
                margin-bottom: 30px;
                .atw_price{
                    color: $theme3;
                }
            }
            .atw_product_actions2{
                padding-bottom:25px;
                li{
                    &:first-child, &:last-child{
                        a{
                            border-radius: 3px;
                            &:hover{
                                background: $theme3;
                                border-color: $theme3;
                            }
                        }
                    }
                    a.atw_btn{
                        border-radius: 3px;
                        background: none;
                        color:#9192a3;
                        border: 1px solid $borderColor;
                        line-height: auto;
                        height: 40px;
                    }
                }
            }
        }
        &:hover{
            border-color: $theme3;
            .atw_item_bottom{
                .atw_product_actions2{
                    a.atw_btn{
                        background: $theme3;
                        color: #fff;
                        border-color: $theme3;
                    }
                }
            }
        }
    }
}